import { Component } from "react";
import { Input } from "../components/Input";
import { Operations } from "../components/Operations";
import { Output } from "../components/Output";
import { Title } from "../components/Title";

export class Greet extends Component {
    constructor() {
        super();
        this.inputs = {
            'first': '',
            'last': ''
        }
        this.message = "";
        this.state = {message: this.message};
    }

    properCase(str) {
        return str.charAt(0).toUpperCase() + str.substring(1).toLowerCase();
    }

    sayWelcome() {
        this.message = `Welcome ${this.properCase(this.inputs['first'])}  ${this.properCase(this.inputs['last'])}`;
        console.log(this.message);
        this.setState({message: this.message});
    }

    takeInput(event, key) {
        this.inputs[key] = event.target.value;
    }

    render() {
        return (<div class='container'>
            <Title/>
            <Input name='first' input={this.takeInput.bind(this)} txt='First Name'/>
            <Input name='last' input={this.takeInput.bind(this)} txt='Last Name'/>
            <br/>
            <Operations call={this.sayWelcome.bind(this)} myClass='btn-primary' txt='Greet'/> &nbsp;
            <Operations myClass='btn-danger' txt='Clear'/>
            <Output msg={this.message}/>
        </div>);
    }
}

